.catalog-sections {
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    margin-bottom: 45px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 71px;
  }

  &__container {
    @media (min-width: $desktop-width) {
      display: grid;
      grid-template-columns: repeat(2, 570px);
      gap: 40px 80px;
      justify-content: center;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    max-width: 708px;
    margin: 0 auto 20px auto;
    padding: 30px 33px 22px 20px;
    background-color: $bg-color;

    @media (min-width: $tablet-width) {
      position: relative;
      margin-bottom: 30px;
      padding: 77px 316px 57px 52px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 0;
      padding-right: 51px;
      padding-bottom: 59px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__title {
    position: relative;
    margin-bottom: 29px;
    padding-left: 71px;
    font-family: $title-font;
    font-weight: 400;
    font-size: 24px;
    line-height: 1.54;
    text-transform: uppercase;
    color: $title-color;

    @media (min-width: $tablet-width) {
      position: static;
      margin-bottom: 24px;
      padding-left: 0;
      font-size: 36px;
      line-height: 1;
    }

    @media (min-width: $desktop-width) {
      padding-left: 162px;
      margin-bottom: 73px;
    }

    &::before {
      position: absolute;
      left: 0;
      top: -5px;
      width: 50px;
      height: 50px;
      content: "";
      background-color: $main-color;
      border-radius: 50%;

      @media (min-width: $tablet-width) {
        top: 90px;
        left: 450px;
        width: 200px;
        height: 200px;
      }

      @media (min-width: $desktop-width) {
        top: 45px;
        left: 60px;
        width: 100px;
        height: 100px;
      }
    }

    &::after {
      position: absolute;
      left: -15px;
      top: -13px;
      z-index: 2;
      width: 75px;
      height: 75px;
      content: "";
      background-position: center;
      background-repeat: no-repeat;

      @media (min-width: $tablet-width) {
        top: 53px;
        left: 420px;
        width: 270px;
        height: 270px;
      }

      @media (min-width: $desktop-width) {
        top: 26px;
        left: 40px;
        width: 134px;
        height: 134px;
      }
    }

    &--slim::after {
      background-image: url("../../img/icons/cat-slim.svg");

      @media (min-width: $tablet-width) {
        background-size: 140px 194px;
      }

      @media (min-width: $desktop-width) {
        background-size: 70px 97px;
      }
    }

    &--pro::after {
      background-image: url("../../img/icons/cat-pro.svg");

      @media (min-width: $tablet-width) {
        background-size: 268px 112px;
      }

      @media (min-width: $desktop-width) {
        background-size: 134px 56px;
      }
    }
  }

  &__text {
    margin-bottom: 19px;
    padding-bottom: 23px;
    font-family: $text-font;
    font-size: 14px;
    line-height: 1.29;
    color: $main-text-color;
    border-bottom: 1px solid #D9D9D9;;

    @media (min-width: $tablet-width) {
      margin-bottom: 52px;
      padding-bottom: 0;
      font-size: 16px;
      line-height: 1.5;
      border-bottom: none;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 27px;
    }
  }

  &__link {
    position: relative;
    font-family: $title-font;
    font-size: 16px;
    line-height: 1;
    text-transform: uppercase;
    color: $title-color;
    text-decoration: none;

    @media (min-width: $tablet-width) {
      font-size: 20px;
      line-height: 1.5;
    }

    &::after {
      position: absolute;
      left: 100px;
      top: 2px;
      width: 24px;
      height: 12px;
      content: "";
      background: url("../../img/icons/arrow.svg") center no-repeat;

      @media (min-width: $tablet-width) {
        left: 120px;
        top: 8px;
      }
    }
  }
}
